import axios from '../axios';
import headers from '../headers';

const getsublevel = async (instanceid: any, levelid: any) => {

    return (await axios.get(`api/v1/appdata/sublevel/info/?instance_id=${instanceid}&level_id=${levelid}`, { "headers": headers(String(localStorage.getItem("AuthToken"))) }));
};

export default getsublevel;